function beforeFirstFrameRender() {
    var didDrawScene = PanelService.didDrawScene;
    if (typeof didDrawScene === 'function') {
        PanelService.didDrawScene = function () {
            Cortona3DSolo.dispatch("core.didDrawAnimationFrame");
            didDrawScene.call(PanelService);
        };
    }
    var willDrawScene = PanelService.willDrawScene;
    if (typeof willDrawScene === 'function') {
        PanelService.willDrawScene = function () {
            Cortona3DSolo.dispatch("core.willDrawAnimationFrame");
            willDrawScene.call(PanelService);
        };
    }
    if (Module.Stella) {
        m_doc = {
            type: 'generic'
        };
    }
    var isGeneric = m_doc.type === 'generic';
    if (!isGeneric) {
        app.pickerTransparencyThreshold = 0;
    }
    app.ui.showCanvas(isGeneric || app.getChildObjects().length > 0);
    Cortona3DSolo.dispatch("core.didChangeLayout");
    app.ui.setBannerImage(app.util.fetchBannerImage());
}

// Tiramisu hooks
expand(Tiramisu, {
    didFinishLoadDocument: function (doc) {
        m_doc = doc;
        attachCanvasListeners();

        doc.baseURL = "";
        if (!doc.bundleURL) {
            doc.baseURL = base.getBaseURL(doc.companionFile || require('app').getDocumentURL());
        } else {
            doc.companionFile = encodeURI(doc.companionFile);
        }

        app.recursiveObjectLookup = (doc.type !== 'ipc' || app.getOption('keep3DStructure') === 'Yes');

        if (app.getOption('KeepSurfaceEdges') === 'Yes') {
            app.edgeColorWeights = [1, 0, 0];
        }

        attachInteractivityXML(doc).then(function () {
            var type = doc.type;
            m_didFinishLoadDocument = true;
            Cortona3DSolo.dispatch('app.didFinishLoadDocument', doc);
            Cortona3DSolo.dispatch('app.didAttachDocument', doc);
            if (m_firstFrameDidArrive) {
                beforeFirstFrameRender();
                setTimeout(function () {
                    Cortona3DSolo.dispatch('app.firstFrameDidArrive');
                }, 0);
            }
        }).catch(function (error) {
            Cortona3DSolo.dispatch('app.didFailLoadDocument', error);
            console.error(error);
        });
    },
    firstFrameDidArrive: function (timestamp) {
        m_firstFrameDidArrive = true;
        if (m_didFinishLoadDocument || Module.Stella) {
            beforeFirstFrameRender();
            Cortona3DSolo.dispatch('app.firstFrameDidArrive', timestamp);
        }
    },
    didFailLoadDocument: function (message) {
        Cortona3DSolo.dispatch('app.didFailLoadDocument', deface(message));
    },
    didClickHyperlink: function (urls) {
        Cortona3DSolo.dispatch('app.didClickHyperlink', urls);
    },
	didChangeVisibility: function(shownHandles, hiddenHandles) {
        Cortona3DSolo.dispatch('app.didChangeVisibility', shownHandles, hiddenHandles);
	},
    willStartNavigateInScene: function () {
        m_velocityX = 0;
        m_velocityY = 0;
        m_isPointerDown = false;
        Cortona3DSolo.dispatch('app.willStartNavigateInScene');
    },
    acquireMouseInput: function () {
        var parentDocument;
        try {
            parentDocument = parent.document;
        } catch (e) { }
        if (parentDocument) {
            m_mouseEventTargets = [];
            parentDocument.querySelectorAll('iframe').forEach(function (i) {
                try {
                    var w = i.contentDocument === document ? parent.window : i.contentWindow;
                    w.addEventListener('mousemove', dispatchMouseEvent);
                    w.addEventListener('mouseup', dispatchMouseEvent);
                    m_mouseEventTargets.push(w);
                } catch (e) { }
            });
        }
    },
    unacquireMouseInput: function () {
        if (m_mouseEventTargets) {
            m_mouseEventTargets.forEach(function (i) {
                try {
                    i.removeEventListener('mousemove', dispatchMouseEvent);
                    i.removeEventListener('mouseup', dispatchMouseEvent);
                } catch (e) { }
            });
            m_mouseEventTargets = null;
        }
    },
});

