        var p = Module._tiramisu_getObjectHighlightParams(type) >> 2;
        return p ? {
            diffuseColor: millicolor([HEAPF32[p], HEAPF32[p + 1], HEAPF32[p + 2]]).toString(),
            diffuseBrightnessDelta: HEAPF32[p + 3],
            emissiveColor: millicolor([HEAPF32[p + 4], HEAPF32[p + 5], HEAPF32[p + 6]]).toString(),
            emissiveBrightnessDelta: HEAPF32[p + 7]
        } : null;
