    /**
     * Sets the 3D object highlight parameters.
     * 
     * @memberof module:solo.app
     * @param {ObjectHighlightEnum} type 
     * @param {ObjectHighlightParams|null} params 
     * @since 2.0.0
     */
    setObjectHighlightParams: function (type, params) {
        if (!params) {
            Module._tiramisu_setObjectHighlightParams(type, 0);
            return;
        }

        var sp = stackSave(),
            mem = stackAlloc(32),
            p = mem >> 2;

        function assign(offset, color, brightness) {
            if (color && color.length > 2) {
                HEAPF32[p + offset] = color[0];
                HEAPF32[p + offset + 1] = color[1];
                HEAPF32[p + offset + 2] = color[2];
                HEAPF32[p + offset + 3] = typeof (brightness) !== 'undefined' ? brightness : 0;
            } else {
                HEAPF32[p + offset] = 0;
                HEAPF32[p + offset + 1] = 0;
                HEAPF32[p + offset + 2] = 0;
                HEAPF32[p + offset + 3] = typeof (brightness) !== 'undefined' ? brightness : 1;
            }
        }

        assign(0, millicolor(params.diffuseColor).toArray(), params.diffuseBrightnessDelta);
        assign(4, millicolor(params.emissiveColor).toArray(), params.emissiveBrightnessDelta);
        Module._tiramisu_setObjectHighlightParams(type, mem);
        stackRestore(sp);
    },

