/**
 * The object represents information about the current IPC sheet, for example:
 * ```
 * {
 *      id: "view_2",
 *      description: "Backplate Assy View",
 *      drawing: "http://.../pump-catalog-2.svg",
 *      dplTableWidth: 69,
 *      dplTable: "<table id=\"dpl-table\" class=\"dpl-table\">...</table>"
 * }
 * ```
 * @prop {string} id - IPC sheet ID. 
 * @prop {string} description - IPC sheet description.
 * @prop {string} drawing - The name of the file for 2D illustration.
 * @prop {number} dplTableWidth - DPL table width.
 * @prop {string} dplTable - The HTML string is a detailed parts list (DPL) in the form of an HTML table.
 * @typedef {object} CatalogCurrentSheetInfo
 * @see {@link module:solo.app.ipc.didSelectSheet app.ipc.didSelectSheet()}
 * @see {@link module:solo.app.ipc.getCurrentSheet app.ipc.getCurrentSheet()}
 */

/**
 * The object visibility code.
 * @prop {number} app.ipc.TRANSITION_ANIMATION_TYPE_FROM_TOP_LEVEL_PARENT=0 The transition animation is built along the chain of parents from the top level. By default.
 * @prop {number} app.ipc.TRANSITION_ANIMATION_TYPE_FROM_PARENT=1 The transition animation is used only from the immediate parent sheet.
 * @prop {number} app.ipc.TRANSITION_ANIMATION_TYPE_LAST_ANIMATION=2 The transition animation is used only for the last animation in the transition to the current sheet.
 * @typedef {number} TransitionAnimationTypeEnum
 * @see {@link module:solo.app.ipc.transitionAnimationType app.ipc.transitionAnimationType}
 */

